import React, { Component, useState } from 'react';
import { Link } from 'react-router-dom';
import { AppBackButton } from '../../components/AppComponents';
import Main from '../Main';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import { Grid } from '@mui/material';

export default class Report extends Component {
    state = {
        permissions: false
    };
    render() {

        let permissionCheck = JSON.parse(localStorage.getItem("permissions"));
        this.state.permissions = permissionCheck.value.admin_dashboard ? permissionCheck.value.admin_dashboard : false;
        console.log('pp', this.state.permissions);

        return (
            <Main MessageRef={this.MessageRef}>
                <div className="grid md:grid-cols-2 gap-2">
                    <div>
                        <AppBackButton to="/">Back to Dashboard</AppBackButton>
                    </div>
                </div>
                <Grid container spacing={2}>
                    <Grid item xs={4}>
                        <Link to="/reports/sales">
                            <Card style={{ borderRadius: '10px', height: '100px', border: "1px solid red" }}>
                                <CardContent style={{ textAlign: 'center', marginTop: '20px' }}>
                                    <p style={{ fontSize: 18 }} >
                                        Sales
                                    </p>
                                </CardContent>
                            </Card>
                        </Link>
                    </Grid>
                    <Grid item xs={4}>
                        <Link to="/reports/emp-sales">
                            <Card style={{ borderRadius: '10px', height: '100px', border: "1px solid red" }}>
                                <CardContent style={{ textAlign: 'center', marginTop: '20px' }}>
                                    <p style={{ fontSize: 18 }} >
                                        Employees Performance
                                    </p>
                                </CardContent>
                            </Card>
                        </Link>
                    </Grid>
                    {this.state.permissions ?
                        <Grid item xs={4}>
                            <Link to="/reports/reseller-sales">
                                <Card style={{ borderRadius: '10px', height: '100px', border: "1px solid red" }}>
                                    <CardContent style={{ textAlign: 'center', marginTop: '20px' }}>
                                        <p style={{ fontSize: 18 }} >
                                            Dashboard
                                        </p>
                                    </CardContent>
                                </Card>
                            </Link>
                        </Grid>
                        : ''}
                </Grid>
            </Main >
        )
    }
}



